.App {  
  text-align: center; 
  align-items: center;
  justify-content: center;
  margin: 100px 0 100px 0;
}

.infoBar {    
  margin: 0 auto;
  padding: 20px 20px;
  width: 300px;
  height: 100px;
  border: 1px;
  border-radius: 6px;
  text-align: left;
  box-shadow: 0 1px 4px;
}



.hljs-variable {
  color: #c82829;
}

.hljs-literal {
  color: #f5871f;
}

.hljs-string {
  color: #718c00;
}